<script>
import Navbar from "../Header/Navbar";
import Footer from "../Header/Footer";
import MediaSide from '../MediaSide'

export default {
  components: {
    MediaSide,
    Footer,
    Navbar
  },
  data() {
    return {
      isActive_m: false,
      dataItem:{}
    }
  },
  mounted() {
    this.$store.dispatch('organization',1)
  },
  methods: {
    clickCallback (pageNum){
     
      this.$store.dispatch('organization',pageNum)
    },
    open_modal() {

    },
    myFunc(item) {
      this.dataItem=item
      this.isActive_m=true
      if(this.isActive_m==true){
        document.body.classList.add('open_modal')
      }
    },
    clouseM(){
      this.isActive_m=false
      if(this.isActive_m==false){
        document.body.classList.remove('open_modal')
      }
    }
  }
}
</script>
